import {Button, Modal, ModalBody, ModalFooter, ModalTitle, Toast} from "react-bootstrap";
import cameraIcon from "../icons/camera.svg";
import React, {useState} from "react";
import ModalHeader from "react-bootstrap/ModalHeader";

const PhotoContainer = ({photoAlbumSet}) => {
    const [showPhotoLibrary, setShowPhotoLibrary] = useState(false)
    const [photo, setPhoto] = useState(undefined);

    const handlePhotoClickFactory = image => e => {
        setPhoto(image);
        setShowPhotoLibrary(false);
    }

    return (
        <>
            <Modal
                show={showPhotoLibrary}
                onHide={() => setShowPhotoLibrary(false)}
            >
                <ModalHeader closeButton>
                    <ModalTitle>Select a photo from your photo library</ModalTitle>
                </ModalHeader>
                <ModalBody>
                    <div className='photo-album-thumbnails'>
                        {photoAlbumSet.map((image) => (
                            <div className='thumbnail-container'>
                                <img
                                    src={image}
                                    onClick={handlePhotoClickFactory(image)}
                                    className="photo-album-thumbnail"
                                />
                            </div>
                        ))}
                    </div>
                </ModalBody>
                <ModalFooter>
                    <Button variant="primary" onClick={() => setShowPhotoLibrary(false)}>Close</Button>
                </ModalFooter>
            </Modal>
            <div className='photo-container'>
                {photo && <img src={photo}/>}
                <div className="buttons">
                    <Button
                        onClick={() => setShowPhotoLibrary(true)}
                        size='sm'
                        variant="light"
                    >
                        <img className='btn-xs-icon' src={cameraIcon}/>
                    </Button>
                </div>
            </div>
        </>

    )
}

export default PhotoContainer;